"use client";
import React, { useMemo, useEffect, useState } from "react";
import { roadmapSteps } from "../data/roadmap"; // You may want to remove this if you're fetching data

// --- Roadmap Component ---
export default function Roadmap() {
  const [steps, setSteps] = useState([]);
  const [error, setError] = useState(null); // New state for error handling

  useEffect(() => {
    const fetchRoadmap = async () => {
      try {
        const response = await fetch("https://51.20.43.58/api/v1/roadmap");
        const data = await response.json();
        setSteps(data); // Assuming the API returns an array of steps
      } catch (error) {
        console.error("Error fetching roadmap data:", error);
        setError("Failed to load roadmap data."); // Set error message
      }
    };

    fetchRoadmap();
  }, []);

  const activeIndex = useMemo(() => {
    return steps.findIndex((s) => !s.isCompleted);
  }, [steps]);

  return (
    <section className="py-10 map-gradient overflow-x-hidden">
      <div className="max-w-5xl mx-auto px-4">
        <h2 className="dark:text-white  text- text-3xl sm:text-4xl font-bold text-center mb-8 text-gray-900">
          Loyihaning Roadmapi
        </h2>
        {error && ( // Display error message if there's an error
          <div className="text-red-500 text-center mb-4">{error}</div>
        )}
        <div className="grid gap-6">
          {steps.map((step, idx) => {
            const isCompleted = step.isCompleted;
            const isActive = idx === activeIndex;
            const isFuture = idx > activeIndex && activeIndex !== -1;

            let containerClass =
              "rounded-2xl p-6 border transition-all duration-300 transform hover:-translate-y-1";
            if (isCompleted) {
              containerClass +=
                " bg-gray-100 border-gray-200 text-gray-600 shadow-sm";
            } else if (isActive) {
              containerClass +=
                " bg-white border-blue-300 shadow-[0_10px_30px_rgba(59,130,246,0.08)] ring-1 ring-blue-100";
            } else if (isFuture) {
              containerClass += " bg-gray-50 border-gray-100 text-gray-500";
            } else {
              containerClass += " bg-white border-gray-200 text-gray-700";
            }

            return (
              <div key={step.id} className={containerClass}>
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-4">
                    <div
                      className={`flex items-center justify-center w-11 h-11 rounded-lg font-semibold text-sm shadow-sm
                        ${
                          isCompleted
                            ? "bg-gray-200 text-gray-500"
                            : isActive
                            ? "bg-gradient-to-br from-blue-500 to-indigo-500 text-white animate-[pulse_2.5s_infinite]"
                            : "bg-white text-gray-800 border border-gray-100"
                        }`}
                    >
                      {step.id}
                    </div>

                    <div>
                      <h3
                        className={`text-lg sm:text-xl font-semibold ${
                          isCompleted ? "text-gray-600" : "text-gray-900"
                        }`}
                      >
                        {step.title}
                      </h3>
                      <p
                        className={`text-sm mt-1 ${
                          isCompleted
                            ? "text-gray-500"
                            : isActive
                            ? "text-blue-600"
                            : "text-gray-500"
                        }`}
                      >
                        {isActive
                          ? "Hozirgi bosqich"
                          : isCompleted
                          ? "Yakunlangan"
                          : "Kutilmoqda"}
                      </p>
                    </div>
                  </div>

                  <div>
                    {isCompleted && (
                      <span className="px-3 py-1 rounded-full text-sm bg-gray-200 text-gray-700">
                        ✅ Tugallandi
                      </span>
                    )}
                    {isActive && (
                      <span className="px-3 py-1 rounded-full text-sm bg-blue-50 text-blue-700 border border-blue-100">
                        Faol
                      </span>
                    )}
                  </div>
                </div>

                <div className="mt-4 space-y-3">
                  <h4 className="font-medium text-gray-700">Tasks:</h4>
                  <ul className="space-y-2">
                    {step.tasks?.map((task) => (
                      <li key={task.id} className="flex items-start gap-3">
                        <div
                          className={`flex-shrink-0 w-5 h-5 mt-0.5 rounded border ${
                            task.completed
                              ? "bg-green-50 border-green-200"
                              : "border-gray-300"
                          }`}
                        >
                          {task.completed && (
                            <svg
                              className="w-full h-full text-green-500"
                              viewBox="0 0 20 20"
                              fill="currentColor"
                            >
                              <path
                                fillRule="evenodd"
                                d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                                clipRule="evenodd"
                              />
                            </svg>
                          )}
                        </div>
                        <span
                          className={`text-sm ${
                            task.completed
                              ? "text-gray-500 line-through"
                              : isActive
                              ? "text-gray-900"
                              : "text-gray-600"
                          }`}
                        >
                          {task.task}
                        </span>
                      </li>
                    ))}
                  </ul>
                </div>

                <p
                  className={`mt-4 font-medium ${
                    isCompleted
                      ? "text-gray-600"
                      : isActive
                      ? "text-indigo-700"
                      : "text-gray-700"
                  }`}
                >
                  {step.result}
                </p>

                <div className="mt-4">
                  <div
                    className={`h-1 rounded-full ${
                      isCompleted
                        ? "bg-gray-200"
                        : isActive
                        ? "bg-gradient-to-r from-blue-400 to-indigo-400 animate-pulse"
                        : "bg-gray-100"
                    }`}
                    style={{ width: isActive ? "72%" : "100%" }}
                  />
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
}
